import React, { Component } from 'react';
import './Menu.css';
import { downloadMD, downloadHTML, downloadPDF } from './Downloads';

class Menu extends Component {

  constructor(props) {
    super(props);

    this.saveMD = this.saveMD.bind(this);
    this.saveHTML = this.saveHTML.bind(this);
    this.savePDF = this.savePDF.bind(this);
  }

  saveMD() {
    // console.log('working')
    downloadMD(
      localStorage.getItem('title') + '.md',
      localStorage.getItem('source')
    );
  }

  saveHTML() {
    downloadHTML(
      localStorage.getItem('title') + '.html',
      localStorage.getItem('compile')
    );
  }

  savePDF() {
    downloadPDF(
      localStorage.getItem('title') + '.pdf',
      localStorage.getItem('source')
    );
  }

  render() {
    return (
      <div className="menu clear">
        <ul className="clear">
          <li>
            <a>Save As <i className="fa fa-angle-down"></i></a>
            <ul className="innerMenu">
              <li onClick={this.saveMD}>Save as .md</li>
              <li onClick={this.saveHTML}>Save as .html</li>
              
            </ul>
          </li>
        </ul>
      </div>
    );
  }
}

export default Menu;



