import React, { Component } from 'react';
import './Header.css';

class Header extends Component {
  constructor (props) {
    super(props);

    this.state = {
      filename: 'My Text'
    }

    this.onfileName = this.onfileName.bind(this);
  }

  componentDidMount () {
    this.setState({
      filename: localStorage.getItem('title')
    });
  }

  onfileName(event) {
    this.setState({
      filename: event.target.value
    });
    localStorage.setItem('title', event.target.value)
  }

  render() {
    return (
      <div className="header">
        <div className="">
          <input className="filename" onChange={this.onfileName} value={this.state.filename}/>
        </div>
      </div>
    );
  }
}

export default Header;



