import React, { Component } from 'react';
import codemirror from 'codemirror';
import showdown from 'showdown';
import TopHeader from './Header';
import Menu from './Menu';
import ReactHtmlParser from 'react-html-parser';
import './Editor.css';
import './codemirror.css';
import './github-markdown.min.css';

let convertor = new showdown.Converter({
  tables: true,
  emoji: true
});

class Editor extends Component {
  constructor (props) {
    super(props);

    this.state = {
      userText: '',
      width: 0,
      height: 0
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentDidMount () {
    // console.log(localStorage.getItem("source"))
    this.code  = codemirror.fromTextArea(this.textareaNode, {
      lineNumbers: true,
      mode: "htmlmixed"
    });
    this.code.getDoc().setValue(localStorage.getItem("source"))
    this.setState({
      userText: localStorage.getItem("compile")
    })
    this.code.on('change', (doc, changes) => {
      this.codemirrorValueChanged(this, doc, changes)
    });
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight - 113});
    document.querySelector('.CodeMirror').style.height = (window.innerHeight - 113) + 'px';
  }

  codemirrorValueChanged (m, doc, change) {
    m.setState({
      userText: convertor.makeHtml(doc.getValue())
    });
    localStorage.setItem("source", doc.getValue());
    localStorage.setItem("compile", convertor.makeHtml(doc.getValue()));
  }

  render() {
    return (
      <div className="editor">
        <Menu></Menu>
        <TopHeader></TopHeader>
        <div className="col wrap">
        	<textarea ref={ref => this.textareaNode = ref} className="full"></textarea>
        </div>
        <div className="col">
          <div className="preview markdown-body" style={{height: `${this.state.height}px`}}>
            {ReactHtmlParser(this.state.userText)}
          </div>
        </div>
      </div>
    );
  }
}

export default Editor;


