import cssStyle from './cssText';


const download = (filename, data, type) => {
  let element = document.createElement('a');
  element.setAttribute('href', type + encodeURIComponent(data));
  element.setAttribute('download', filename);

  element.style.display = 'none';
  document.body.appendChild(element);
  element.click();
  document.body.removeChild(element);
}

export const downloadMD = (filename, markdownData) => {
  download(filename, markdownData, 'data:text/plain;charset=utf-8,')
}

export const downloadPDF = (filename, markdownData) => {
  
}


export const downloadHTML = (filename, htmlData) => {
  let html = `
  <html>
  <head>
    <title>${filename}</title>
    <style>
      ${cssStyle}
      .markdown-body {
        max-width: 980px;
        margin: 0 auto;
      }
    </style>
  </head>
  <body>
    <div class="markdown-body">
    ${htmlData}
    </div>
  </body>
  </html>
  `;

  download(filename, html, 'data:text/html;charset=utf-8,')
}


